        <img class="eatandgo" :src="imageSrc" alt="" srcset="">
      </div>
      <div class="wrapper--input">
       
        <input v-model="user_search_restaurant" type="text" placeholder="De quoi avez vous besoin ?">
        
          <div class="search-results" v-if="user_search_restaurant.length > 0">
        <div class="search">
            <RouterLink class="container--restaurant--search"  v-for="(restaurant, i) in search_restaurant" :key="i" :to="{name: 'Restaurant' , params: {name: restaurant.name } }" >  
           <div >
            <h2>{{restaurant.name}}</h2>
            <div class="wrapper--img">
              <img :src="restaurant.image" alt="" srcset="" class="image">
            </div>
          </div></RouterLink>
            </div>
         
        </div>
      </div>
      </div>
         
    <nav>
      <ul><li><a href="#">COMMANDEZ MAINTENANT</a></li>
        <li><a href="#">MENU GENERAL</a></li>
        <li>
          <a href="#">CATEGORIES SPECIALES</a>
          <ul>
             <li><a href="#">SPECIALITE</a></li>
            <li><a href="#">HEALTHY</a></li>
            <li><a href="#">GOURMAND</a></li>
            <li><a href="#">MENU ENFANT</a></li>
            <li><a href="#">EN PROMOTION</a></li>
          </ul>
        </li>
      <router-link to="/Reservation"> <li><a href="#">RESERVATION</a></li>
        </router-link> 


      <li><a href="#">A PROPOS</a> 
      <ul>
       <li> <RouterLink  to="/NosRestaurants">  NOS RESTAURANTS </RouterLink></li> 
     <li><RouterLink  to="/ContactezNous"> CONTACTEZ - NOUS</RouterLink></li>
      </ul> 
          </li>
          </ul>


    </nav>
