<script>
import BDD from '../BDD'
import { onMounted,ref,watch } from 'vue'
// Components
import RestaurantRow from '../components/RestaurantRow.vue' 
export default {
name: 'HomeAccueil', 
components:{
   RestaurantRow

  },
//debut du travail avec la BDcommeent dec
setup(){ 

    //creation de la classe restaurant   
 class Restaurant{
      constructor(name,note,image,drive_time){
        this.name=name
        this.note=note
        this.image=image
        this.drive_time=drive_time 
      }
    
    }
    //tableau pour les donnees de la BD
    let data_restaurant = ref([]);
   let all_restaurant =[];
    //transfert des donnees avec une fonction mkdrestaurant
      const makeDataRestaurant = () => {  
        let three_restaurants = [] ;
        //boucle sur chaque objet de la BD
        for(const restaurant of BDD){
          //creation des objets qui vont contenir les info de la BD
          const new_restaurant = new Restaurant(restaurant.name,restaurant.note,restaurant.image,restaurant.drive_time)
        all_restaurant.push(new_restaurant);
          //creation d'un tableau contenant 3 restaurants 
        // push nous permet de faire rentrer les elements dans notre tableau
        three_restaurants.push(new_restaurant);
        if(three_restaurants.length==2){
          three_restaurants.push(new_restaurant);
          data_restaurant.value.push(three_restaurants);
          three_restaurants=[];
        }
          else{
            three_restaurants.push(new_restaurant); 
          }
        }
      } 

// recherche

let user_search_restaurant = ref('');
let search_restaurant = ref([]);

watch(user_search_restaurant,new_value => {
    let regex =RegExp(new_value.toLowerCase()) ;
    let new_search_restaurant= all_restaurant.filter(restaurant =>regex.test(restaurant.name.toLowerCase()));
    new_value == 0 ? search_restaurant.value= [] : search_restaurant.value = new_search_restaurant;

  } ) ,

     onMounted(makeDataRestaurant);
      //return
      return{
       imageSrc:  require("../components/logo.jpg"),
      Banniere: require("../components/banniere.jpeg"),
      user_search_restaurant,
    search_restaurant
      }
    },
}

</script>
  
