<template>
    <div class="Restaurant--row" ><br>
        <h2 class="title">
            NOS RESTAURANTS
        </h2>
        <div class="wrapper--card">
          <RouterLink  v-for="(data, index) in three_restaurants" :key="index" :to="{name: 'Restaurant' , params: {name: data.name } }">
            <Restaurant-card   :info_restaurant="data"/>
       </RouterLink> </div>
    </div>
    </template>
<script>
import BDD from '../BDD'
import { onMounted,ref } from 'vue'
import RestaurantCard from './RestaurantCard.vue';
export default {
    name: "RestaurantRow",
    components: {
       RestaurantCard
    }
    ,setup(){ 
class Restaurant{
      constructor(name,note,image,drive_time){
        this.name=name
        this.note=note
        this.image=image
        this.drive_time=drive_time 
      }
    }
    //tableau pour les donnees de la BD
    let data_restaurant = ref([]);
      let three_restaurants = ref([]) ; 
       //transfert des donnees avec une fonction mkdrestaurant
      const makeDataRestaurant = () => {  
        
        //boucle sur chaque objet de la BD
        for(const restaurant of BDD){
          //creation des objets qui vont contenir les info de la BD
         const new_restaurant = new Restaurant(restaurant.name,restaurant.note,restaurant.image,restaurant.drive_time)
        //creation d'un tableau contenant 3 restaurants 
        // push nous permet de faire rentrer les elements dans notre tableau
        if(three_restaurants.value.length==3){
          three_restaurants.value.push(new_restaurant);
          data_restaurant.value.push(three_restaurants);
          //three_restaurants.value=[];
        }
          else{
            three_restaurants.value.push(new_restaurant); 
          }
        }
      } 

      onMounted(makeDataRestaurant);
      //return
      return{
      three_restaurants,
      data_restaurant,
      }
}}
    


</script>
<style lang="scss">
.Restaurant--row .wrapper--card {
  
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    text-decoration: none !important;
    flex-basis: calc(33.33% - 10px); /* 10px est la somme de la marge entre les éléments */
    margin-bottom: 10px; /* La marge entre les éléments */
  }
  
 
.title{
  padding-bottom: 2%;
 font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
 
}
</style>