import { createApp } from 'vue'
import App from './App.vue'


 
import {createRouter, createWebHashHistory} from 'vue-router'
import Home from './page/home.vue'
import Restaurant from './page/restaurant.vue'
import Reservation from './page/Reservation.vue'
import ContactezNous from './page/ContactezNous'
import NosRestaurants from './page/NosRestaurants'



 const routes = [
   
 { path: '/', component: Home },
{  name: 'Restaurant', path: '/restaurant/:name' , component: Restaurant},
{name :  'Reservation ', path:'/Reservation' , component:  Reservation},
{name : 'ContactezNous', path : '/ContactezNous', component : ContactezNous},
{name : 'NosRestaurants', path : '/NosRestaurants', component : NosRestaurants},
]  
   
const router = createRouter ( {
history: createWebHashHistory(),
routes
});

const VueApp = createApp(App)

VueApp.use(router)

VueApp.mount('#app');



 
